"use client";

import React, { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";

interface TimeLeft {
    days: number;
    hours: number;
    minutes: number;
    seconds: number;
}

import KenBurnsSlideshow from "@/components/core/KenBurnsSlideshow";

export default function Countdown() {
    const targetDate = new Date("2026-03-07T16:00:00").getTime();
    const [timeLeft, setTimeLeft] = useState<TimeLeft>({ days: 0, hours: 0, minutes: 0, seconds: 0 });

    const slideshowImages = [
        "/assets/gallery/gallery-1.jpeg",
        "/assets/gallery/gallery-2.jpeg",
        "/assets/gallery/gallery-3.jpeg",
        "/assets/gallery/gallery-4.jpeg",
        "/assets/gallery/gallery-5.jpeg",
    ];

    useEffect(() => {
        const timer = setInterval(() => {
            const now = new Date().getTime();
            const distance = targetDate - now;

            if (distance < 0) {
                clearInterval(timer);
                setTimeLeft({ days: 0, hours: 0, minutes: 0, seconds: 0 });
            } else {
                setTimeLeft({
                    days: Math.floor(distance / (1000 * 60 * 60 * 24)),
                    hours: Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
                    minutes: Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60)),
                    seconds: Math.floor((distance % (1000 * 60)) / 1000),
                });
            }
        }, 1000);

        return () => clearInterval(timer);
    }, [targetDate]);

    return (
        <section className="relative flex min-h-[80vh] flex-col items-center justify-center py-24 px-4 overflow-hidden">
            {/* Cinematic Background */}
            <KenBurnsSlideshow
                images={slideshowImages}
                overlayColor="var(--overlay-dark)"
                overlayOpacity={0.4}
            />

            <motion.h2
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                className="mb-12 font-display text-4xl text-white md:text-5xl lg:text-6xl text-center drop-shadow-lg"
            >
                Counting Down To Forever
            </motion.h2>

            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 md:gap-8 max-w-5xl w-full">
                <CountdownCard label="DAYS" value={timeLeft.days} />
                <CountdownCard label="HOURS" value={timeLeft.hours} />
                <CountdownCard label="MINUTES" value={timeLeft.minutes} />
                <CountdownCard label="SECONDS" value={timeLeft.seconds} />
            </div>

            <motion.p
                initial={{ opacity: 0 }}
                whileInView={{ opacity: 1 }}
                viewport={{ once: true }}
                transition={{ delay: 0.5 }}
                className="mt-12 font-accent text-2xl text-cream italic drop-shadow-md"
            >
                March 07, 2026 â€¢ New Delhi, India
            </motion.p>
        </section>
    );
}

function CountdownCard({ label, value }: { label: string; value: number }) {
    return (
        <motion.div
            initial={{ opacity: 0, scale: 0.9 }}
            whileInView={{ opacity: 1, scale: 1 }}
            viewport={{ once: true }}
            className="relative flex flex-col items-center justify-center rounded-2xl bg-white/10 p-6 md:p-8 shadow-2xl backdrop-blur-xl border border-white/20"
        >
            <div className="relative h-20 md:h-24 w-full flex items-center justify-center overflow-hidden">
                <AnimatePresence mode="popLayout">
                    <motion.span
                        key={value}
                        initial={{ y: 40, opacity: 0 }}
                        animate={{ y: 0, opacity: 1 }}
                        exit={{ y: -40, opacity: 0 }}
                        transition={{ type: "spring", damping: 15, stiffness: 200 }}
                        className="font-display text-5xl md:text-6xl font-bold text-white drop-shadow-lg"
                    >
                        {value.toString().padStart(2, "0")}
                    </motion.span>
                </AnimatePresence>
            </div>
            <span className="mt-2 font-body text-xs md:text-sm font-semibold tracking-[0.2em] text-cream uppercase">
                {label}
            </span>
        </motion.div>
    );
}
