"use client";

import React, { useState, useRef } from "react";
import { motion, AnimatePresence, useScroll, useTransform } from "framer-motion";
import Image from "next/image";
import { X, ChevronLeft, ChevronRight, Maximize2 } from "lucide-react";

const photos = [
    "/assets/gallery/gallery-1.jpeg",
    "/assets/gallery/gallery-2.jpeg",
    "/assets/gallery/gallery-3.jpeg",
    "/assets/gallery/gallery-4.jpeg",
    "/assets/gallery/gallery-5.jpeg",
    "/assets/gallery/gallery-1.jpeg",
    "/assets/gallery/gallery-2.jpeg",
    "/assets/gallery/gallery-3.jpeg",
    "/assets/gallery/gallery-4.jpeg",
];

export default function Gallery() {
    const [selectedImage, setSelectedImage] = useState<number | null>(null);

    const prevImage = () => {
        if (selectedImage !== null) {
            setSelectedImage((selectedImage - 1 + photos.length) % photos.length);
        }
    };

    const nextImage = () => {
        if (selectedImage !== null) {
            setSelectedImage((selectedImage + 1) % photos.length);
        }
    };

    return (
        <section className="bg-[#1A1A1A] py-24 px-4 overflow-hidden">
            <div className="max-w-7xl mx-auto">
                <motion.div
                    initial={{ opacity: 0, scale: 0.95 }}
                    whileInView={{ opacity: 1, scale: 1 }}
                    viewport={{ once: true }}
                    className="text-center mb-16"
                >
                    <h2 className="font-script text-6xl text-white mb-4 drop-shadow-lg">Captured Moments</h2>
                    <p className="font-accent text-xl text-gold italic drop-shadow-md">A glimpse into our journey together</p>
                </motion.div>

                {/* Parallax Grid */}
                <div className="columns-1 sm:columns-2 lg:columns-3 gap-6 space-y-6">
                    {photos.map((src, index) => (
                        <ParallaxImage
                            key={index}
                            src={src}
                            index={index}
                            onClick={() => setSelectedImage(index)}
                        />
                    ))}
                </div>
            </div>

            {/* Lightbox */}
            <AnimatePresence>
                {selectedImage !== null && (
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        className="fixed inset-0 z-[10000] flex items-center justify-center bg-black/95 backdrop-blur-xl px-4"
                    >
                        <button
                            onClick={() => setSelectedImage(null)}
                            className="absolute top-8 right-8 text-white/70 hover:text-white transition-colors z-50"
                        >
                            <X className="h-10 w-10" />
                        </button>

                        <button
                            onClick={prevImage}
                            className="absolute left-4 top-1/2 -translate-y-1/2 text-white/50 hover:text-white transition-colors p-2 z-50"
                        >
                            <ChevronLeft className="h-12 w-12" />
                        </button>

                        <button
                            onClick={nextImage}
                            className="absolute right-4 top-1/2 -translate-y-1/2 text-white/50 hover:text-white transition-colors p-2 z-50"
                        >
                            <ChevronRight className="h-12 w-12" />
                        </button>

                        <motion.div
                            key={selectedImage}
                            initial={{ scale: 0.9, opacity: 0 }}
                            animate={{ scale: 1, opacity: 1 }}
                            exit={{ scale: 0.9, opacity: 0 }}
                            className="relative max-w-5xl max-h-[85vh] w-full h-full"
                        >
                            <Image
                                src={photos[selectedImage]}
                                alt="Selected Image"
                                fill
                                className="object-contain"
                            />
                        </motion.div>

                        <div className="absolute bottom-10 left-1/2 -translate-x-1/2 text-white/70 font-display text-lg">
                            {selectedImage + 1} / {photos.length}
                        </div>
                    </motion.div>
                )}
            </AnimatePresence>
        </section>
    );
}

function ParallaxImage({ src, index, onClick }: { src: string; index: number; onClick: () => void }) {
    const ref = useRef(null);
    const { scrollYProgress } = useScroll({
        target: ref,
        offset: ["start end", "end start"]
    });

    const y = useTransform(scrollYProgress, [0, 1], [0, index % 2 === 0 ? -40 : 40]);

    return (
        <motion.div
            ref={ref}
            style={{ y }}
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ delay: (index % 3) * 0.1, duration: 0.8 }}
            className="relative group cursor-pointer overflow-hidden rounded-2xl bg-white/5 border border-white/10"
            onClick={onClick}
        >
            <Image
                src={src}
                alt={`Gallery image ${index + 1}`}
                width={600}
                height={800}
                className="w-full h-auto object-cover transition-transform duration-700 group-hover:scale-105"
            />
            <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity flex flex-col items-center justify-center">
                <div className="bg-white/20 backdrop-blur-md p-4 rounded-full border border-white/30 transform scale-75 group-hover:scale-100 transition-transform duration-500">
                    <Maximize2 className="text-white h-6 w-6" />
                </div>
            </div>

            {/* Elegant Border Reveal */}
            <div className="absolute inset-0 border border-gold/0 group-hover:border-gold/30 transition-colors duration-500 pointer-events-none rounded-2xl" />
        </motion.div>
    );
}
