"use client";

import React, { useRef } from "react";
import { motion, useScroll, useTransform } from "framer-motion";
import VideoBackground from "@/components/core/VideoBackground";
import { Sparkles } from "lucide-react";

export default function Hero() {
    const containerRef = useRef(null);
    const { scrollYProgress } = useScroll({
        target: containerRef,
        offset: ["start start", "end start"],
    });

    const opacity = useTransform(scrollYProgress, [0, 1], [1, 0]);

    const names = "Shubham & Varsha";

    return (
        <section
            ref={containerRef}
            className="relative h-screen w-full overflow-hidden"
        >
            {/* Cinematic Video Background */}
            <VideoBackground
                src="https://assets.mixkit.co/videos/preview/mixkit-wedding-couple-walking-in-a-forest-44456-large.mp4"
                overlayColor="var(--overlay-dark)"
                overlayOpacity={0.5}
                parallax={true}
                speed={0.6}
            />

            {/* Content */}
            <motion.div
                style={{ opacity }}
                className="relative z-10 flex h-full flex-col items-center justify-center px-4 text-center"
            >
                <div className="mb-6 overflow-hidden">
                    <motion.h1
                        initial={{ y: 100 }}
                        animate={{ y: 0 }}
                        transition={{ duration: 1, ease: [0.33, 1, 0.68, 1] }}
                        className="font-script text-6xl text-white md:text-8xl lg:text-9xl drop-shadow-[0_2px_20px_rgba(0,0,0,0.8)]"
                    >
                        {names.split("").map((char, i) => (
                            <motion.span
                                key={i}
                                initial={{ opacity: 0 }}
                                animate={{ opacity: 1 }}
                                transition={{ duration: 0.1, delay: 0.5 + i * 0.05 }}
                            >
                                {char}
                            </motion.span>
                        ))}
                    </motion.h1>
                </div>

                <motion.p
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 1, delay: 1.5 }}
                    className="mb-8 font-display text-xl uppercase tracking-[0.2em] text-cream md:text-2xl drop-shadow-md"
                >
                    We're Getting Married!
                </motion.p>

                <motion.div
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{ duration: 1, delay: 2.5 }}
                    className="flex flex-col items-center"
                >
                    <div className="mb-2 h-px w-24 bg-gold shadow-gold-sm" />
                    <p className="font-body text-lg font-medium tracking-widest text-white drop-shadow-md">
                        MARCH 07, 2026
                    </p>
                    <div className="mt-2 h-px w-24 bg-gold shadow-gold-sm" />
                </motion.div>

                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 3, duration: 1 }}
                    className="flex flex-col md:flex-row gap-6 mt-12"
                >
                    <a href="/invitation" className="inline-block">
                        <motion.button
                            whileHover={{ scale: 1.05 }}
                            whileTap={{ scale: 0.95 }}
                            className="px-10 py-4 bg-gradient-to-r from-dusty-rose to-rose-gold text-white rounded-full font-body text-sm tracking-[0.2em] shadow-lg hover:shadow-2xl transition-all flex items-center gap-2"
                        >
                            VIEW LIVE INVITE <Sparkles className="h-4 w-4" />
                        </motion.button>
                    </a>
                    <motion.button
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                        onClick={() => document.getElementById('rsvp')?.scrollIntoView({ behavior: 'smooth' })}
                        className="px-10 py-4 bg-white/10 backdrop-blur-md text-white rounded-full font-body text-sm tracking-[0.2em] shadow-lg hover:shadow-2xl transition-all border border-white/20"
                    >
                        RSVP NOW
                    </motion.button>
                </motion.div>

                {/* Scroll Indicator */}
                <motion.div
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{ duration: 1, delay: 3.5 }}
                    className="absolute bottom-10 left-1/2 -translate-x-1/2"
                >
                    <motion.div
                        animate={{ y: [0, 10, 0] }}
                        transition={{ duration: 2, repeat: Infinity, ease: "easeInOut" }}
                        className="flex flex-col items-center gap-2"
                    >
                        <span className="font-accent text-sm tracking-widest text-cream uppercase">SCROLL</span>
                        <div className="h-12 w-[1px] bg-gradient-to-b from-white to-transparent" />
                    </motion.div>
                </motion.div>
            </motion.div>
        </section>
    );
}
